import React, {Component} from 'react'
import {connect} from 'react-redux'
import {loadProducts, addCart} from 'Actions'
import ProductListItem from 'Components/ProductListItem'
import CircularProgress from 'material-ui/CircularProgress'

class Catalog extends Component{
	componentDidMount(){
		if( !this.props.loaded && !this.props.loading )
			this.props.loadProducts()
	}
	render(){

		const {loading, loaded, phones, addCart} = this.props

		function getBody(){
			if( loading && !loaded ) return <CircularProgress size={60} thickness={7} />
			return(
				<div className="row">
					{phones.map((phone) => {
						return(
							<div className="col-md-4" key={phone.id}>
								<ProductListItem 
									id={phone.id}
									name={phone.name}
									link={phone.id}
									price={phone.price}
									srcImg={phone.image}
									funcAddCart={ addCart }
								/>
							</div>
						)
					})}
				</div>
			)
		}
		return(
			<div>
				{getBody()}
			</div>
		)
	}
}


const mapDispatchToProps = (dispatch) => {
	return({
		loadProducts: () => dispatch( loadProducts() ),
		addCart: (price, id) => dispatch( addCart(price, id) )
	})
}
const mapStateToProps = (state) => {
	return{
		loading: state.loadProducts.loading,
		loaded: state.loadProducts.loaded,
		phones: state.loadProducts.phones
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Catalog)


