import React, {Component} from 'react'
import Checkbox from 'material-ui/Checkbox'

class FilterCheckboxes extends Component{
	render(){
		return(
			<div className="b-filter-checkboxes">
				<Checkbox
          label="Apple"
          onClick={this.props.onChangeCheckboxes.bind(null, 'Apple')}
        />
        <Checkbox
          label="Samsung"
          onClick={this.props.onChangeCheckboxes.bind(null, 'Samsung')}
        />
        <Checkbox
          label="Lenovo"
          onClick={this.props.onChangeCheckboxes.bind(null, 'Lenovo')}
        />
        <Checkbox
          label="Nokia"
          onClick={this.props.onChangeCheckboxes.bind(null, 'Nokia')}
        />
			</div>
		)
	}
}

export default FilterCheckboxes


